:root {
  --main-bg-color: #ffffff; /* Light mode background */
  --main-text-color: #000000; /* Light mode text */
  --table-head-bg: #1976d2;
  --button-green-bg: #4caf50; /* Light mode green button */
  --button-green-hover-bg: #45a049; /* Light mode green button hover */
  --button-red-bg: #f44336; /* Light mode red button */
  --button-red-hover-bg: #d32f2f; /* Light mode red button hover */
  --button-green-disabled-bg: #37474f; /* Light mode green button disabled */
  --button-red-disabled-bg: #37474f; /* Light mode red button disabled */
}

.darkMode {
  background-color: #121212; /* Dark mode background */
  color: #ffffff; /* Dark mode text */
  --main-bg-color: #121212; /* Dark mode background */
  --main-text-color: #ffffff; /* Dark mode text */
  --table-head-bg: #121212;
  --button-green-bg: #388e3c; /* Dark mode green button */
  --button-green-hover-bg: #66bb6a; /* Dark mode green button hover */
  --button-red-bg: #d32f2f; /* Dark mode red button */
  --button-red-hover-bg: #ef5350; /* Dark mode red button hover*/
  --button-green-disabled-bg: #37474f; /* Dark mode green button disabled */
  --button-red-disabled-bg: #37474f; /* Dark mode red button disabled */
}

.transition {
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Button styles */
.redButton {
  background-color: var(--button-red-bg) !important;
  color: #ffffff !important;
  transition: background-color 0.3s;
}

.redButton:hover {
  background-color: var(--button-red-hover-bg) !important;
}

.redButton:disabled {
  background-color: var(--button-red-disabled-bg);
  color: #ffffff; /* Disabled text color */
}

.greenButton {
  background-color: var(--button-green-bg) !important;
  color: #ffffff !important;
  transition: background-color 0.3s;
}

.greenButton:hover {
  background-color: var(--button-green-hover-bg) !important;
}

.greenButton:disabled {
  background-color: var(--button-green-disabled-bg);
  color: #ffffff; /* Disabled text color */
}

body {
  margin: 0;
  transition: background-color 0.3s ease, color 0.3s ease;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.tableHead {
  background-color: var(--table-head-bg) !important;
  color: #ffffff !important;
}
